import React from "react";
import { useState } from "react";
import ReactCardFlip from "react-card-flip";

function Card({ id, src, teamColor }) {
  const [isFlipped, setIsFlipped] = useState(false);

  return (
    <ReactCardFlip isFlipped={isFlipped} flipDirection="vertical">
      <div onClick={() => setIsFlipped(!isFlipped)}>
        <img
          className="h-[50px] w-[30px] md:h-[70px] md:w-[50px] lg:h-[99px] lg:w-[80px] m-[2px] lg:m-[4px] hover:scale-105 duration-200 shadow-sm"
          id={id}
          src={src}
          alt="cards"
        />
      </div>
      <div
        className={`h-[50px] w-[30px] md:h-[70px] md:w-[50px] lg:h-[99px] lg:w-[80px] m-[2px] lg:m-[4px] rounded-sm shadow-sm bg-red-500`}
        onClick={() => setIsFlipped(!isFlipped)}
      ></div>
    </ReactCardFlip>
  );
}

export default Card;
