import React, { useState, useEffect } from "react";
import { data } from "../CardData.js";
import Card from "./Card.js";
import { useSelector } from "react-redux";

const Grid = () => {
  const [cards, setCards] = useState([]);

  const teamColor = useSelector((state) => state.user.selectedTeam);

  useEffect(() => {
    setCards(data.cards);
  }, []);

  return (
    <div className="grid grid-cols-10 mx-2">
      {cards.map((card) => (
        <Card id={card.id} src={card.src} teamColor={teamColor} />
      ))}
    </div>
  );
};

export default Grid;
